import React from "react";
import { navigate } from "gatsby";
import { Pagination } from "semantic-ui-react";
import qs from "qs";

export default function ({ totalPages, tag, page }) {
  return (
    <Pagination
      pointing
      secondary
      firstItem={null}
      lastItem={null}
      totalPages={totalPages}
      activePage={page}
      onPageChange={(e, { activePage }) =>
        navigate("/?" + qs.stringify({ tag, page: activePage }))
      }
    />
  );
}
