import React from "react";
import { Link } from "gatsby";
import { Card, Icon } from "semantic-ui-react";
import Tags from "./Tags";

export default function ({ item }) {
  const {
    excerpt,
    timeToRead,
    fields: { slug, tags },
    frontmatter: { title },
  } = item;
  return (
    <Card fluid link style={{ cursor: "default" }}>
      <Card.Content>
        <Card.Header as={Link} to={slug}>
          {title}
        </Card.Header>
        <Card.Meta>
          <Icon name="clock outline" size="small" /> {timeToRead}min
        </Card.Meta>
        <Card.Description>
          {excerpt}
          <br />
        </Card.Description>
      </Card.Content>
      <Card.Content extra>
        <Tags tags={tags} />
      </Card.Content>
    </Card>
  );
}
