import { escapeRegExp } from "lodash";
import React, { useState } from "react";
import styled from "styled-components";
import { useStaticQuery, graphql, navigate } from "gatsby";
import { Search } from "semantic-ui-react";

const SectionStyled = styled.section`
  margin-left: 1rem;
  align-self: center;
  display: none;
  @media (min-width: 992px) {
    display: block;
  }
`;

export default function PostSearch() {
  const {
    allMarkdownRemark: { nodes },
  } = useStaticQuery(graphql`
    query listPostForSearch {
      allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
        nodes {
          fields {
            slug
          }
          frontmatter {
            title
          }
        }
      }
    }
  `);

  const posts = nodes.map(({ fields: { slug }, frontmatter: { title } }) => ({
    slug,
    title,
  }));

  const [value, setValue] = useState("");
  const [results, setResults] = useState([]);

  return (
    <Search
      as={SectionStyled}
      placeholder="Search posts..."
      input={{ inverted: true, iconPosition: "left", size: "large" }}
      loading={false}
      value={value}
      results={results}
      onSearchChange={(e, { value }) => {
        setValue(value);
        if (value.length < 2) return setResults([]);

        const re = new RegExp(escapeRegExp(value), "i");
        setResults(posts.filter((post) => re.test(post.title)));
      }}
      onResultSelect={(e, { result }) => navigate(result.slug)}
    />
  );
}
