import React from "react";
import PropTypes from "prop-types";
import { Link } from "gatsby";
import { Segment, Menu, Container, Header, Icon } from "semantic-ui-react";
import qs from "qs";
import { Search } from "../Post";

export default function PageHeader() {
  const search =
    typeof document === `undefined` ? "" : document.location.search;
  const { tag = null } = qs.parse(search, {
    ignoreQueryPrefix: true,
  });
  return (
    <Segment as="header" inverted vertical>
      <Menu size="small" inverted stackable>
        <Container>
          <Menu.Item as={Link} to="/" header>
            <Header color="teal" inverted>
              <Icon name="file code" />
              <Header.Content>
                Engineering Blog
                <Header.Subheader>by Dale Seo</Header.Subheader>
              </Header.Content>
            </Header>
          </Menu.Item>

          <Menu.Item as={Link} to="/?tag=GraphQL" active={tag === "GraphQL"}>
            <Icon name="globe" />
            GraphQL
          </Menu.Item>
          <Menu.Item as={Link} to="/?tag=React" active={tag === "React"}>
            <Icon name="react" />
            React
          </Menu.Item>
          <Menu.Item
            as={Link}
            to="/?tag=JavaScript"
            active={tag === "JavaScript"}
          >
            <Icon name="js" />
            JavaScript
          </Menu.Item>
          <Menu.Item as={Link} to="/?tag=Python" active={tag === "Python"}>
            <Icon name="python" />
            Python
          </Menu.Item>
          <Menu.Item as={Link} to="/?tag=Java" active={tag === "Java"}>
            <Icon name="coffee" />
            Java
          </Menu.Item>
          <Menu.Item as={Link} to="/?tag=Docker" active={tag === "Docker"}>
            <Icon name="docker" />
            Docker
          </Menu.Item>

          <Menu.Menu position="right">
            <Search />
          </Menu.Menu>
        </Container>
      </Menu>
    </Segment>
  );
}

PageHeader.propTypes = {
  activeItem: PropTypes.string,
};

PageHeader.defaultProps = {
  activeItem: "",
};
