import React from "react";
import { Container, Card, Header } from "semantic-ui-react";
import Item from "./Item";

export default function List({ title, totalPosts, posts }) {
  return (
    <Container text>
      <Header as="h1" dividing>
        {title}
        <Header.Subheader>{totalPosts} posts</Header.Subheader>
      </Header>
      <Card.Group>
        {posts.map((post) => (
          <Item key={post.fields.slug} item={post} />
        ))}
      </Card.Group>
    </Container>
  );
}
