import React from "react";
import styled from "styled-components";
import { useStaticQuery, graphql, Link } from "gatsby";
import { Segment, Menu, Label, Icon } from "semantic-ui-react";

import koreanTags from "../../../data/koreanTags.json";

const koreanRegex = /[-|-|-]/;

const SidebarStyled = styled.aside`
  max-width: 300px;
  display: none;
  @media (min-width: 992px) {
    display: block;
  }
`;

export default function Sidebar() {
  const {
    allMarkdownRemark: { group: tags },
  } = useStaticQuery(graphql`
    query groupTags {
      allMarkdownRemark {
        group(field: fields___tags) {
          fieldValue
          totalCount
        }
      }
    }
  `);

  const topTags = tags
    .filter(({ fieldValue }) => !koreanRegex.test(fieldValue))
    .sort((a, b) => b.totalCount - a.totalCount)
    .slice(0, 50);

  return (
    <SidebarStyled>
      <Segment basic>
        <Menu size="small" fluid vertical>
          <Menu.Item header active>
            <Icon name="tags" /> Tags
          </Menu.Item>
          {topTags.map(({ fieldValue, totalCount }) => (
            <Menu.Item key={fieldValue} as={Link} to={`/?tag=${fieldValue}`}>
              <Label size="small">{totalCount}</Label>
              {koreanTags[fieldValue] || fieldValue}
            </Menu.Item>
          ))}
        </Menu>
      </Segment>
    </SidebarStyled>
  );
}
